import { createRouter, createWebHistory } from "vue-router";

import MainPage from '@/pages/MainPage'

export const routers = createRouter({
    history: createWebHistory(),
    routes:[
    { path: '/', name: 'MainPage', component: MainPage},
    { path: '/users', name: 'Users', component: () => import('@/pages/Users.vue')},
    { path: '/todos', name: 'Todos', component: () => import('@/pages/Todos.vue')},
    { path: '/posts', name: 'Posts', component: () => import('@/pages/Posts.vue')},
    { path: '/:id', name: 'PagesItem', component: () => import('@/pages/PagesItem.vue')},
    { path: '/:catchAll(.*)', name: '404', component: () => import('@/pages/NotFound.vue')},
    ]
})